import React from 'react';
import {Select, Table} from "antd";
import {bindActionCreators} from "redux";
import {editProfile, getUserDetail} from "../actions/userDetail.action";
import {connect} from "react-redux";
import {changeStage, getAllDeliveryInfo} from "../actions/deliveryInfo.action";

const { Option } = Select;

class Orders extends React.Component {

  componentDidMount() {
    this.props.getAllDeliveryInfo();
  }


  changeStage = e => {
    this.props.changeStage(e, (res) => {
      if (res.data && res.data.success) {
        this.props.history.push('/orders')
      } else {
      }
    });
    console.log(e);
  };

  stageOnChange = e => {
    console.log(e);
  };

  render () {

    const columns = [
      {
        title: 'Order ID',
        key: 'orderId',
        render: (record) =>
          <div>#{record.orderNum}</div>
      },
      {
        title: 'User ID',
        dataIndex: 'userId',
        key: 'userID',
      },
      {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Purchase Date',
        dataIndex: 'time',
        key: 'time',
      },
      {
        title: 'Total',
        key: 'total',
        render: (record) =>
          <div>$ {parseFloat(record.total * 1.23).toFixed(2)}</div>
      },
      {
        title: 'Stage',
        width: '15%',
        render: (record) =>
          <Select
            placeholder={record.stage}
            onSelect={() => this.changeStage(record)}
            onChange={(stage) => this.stageOnChange(record.stage = stage)}
          >
            <Option value={0}>0</Option>
            <Option value={1}>1</Option>
            <Option value={2}>2</Option>
          </Select>
      },
    ];

    return (

      <div>
        {
          this.props.deliveryInfo
            ? <Table pagination={false} columns={columns} dataSource={this.props.deliveryInfo} />
            : null
        }
      </div>

    );

  }

}

function mapStateToProps(store) {
  return {
    deliveryInfo: store.deliveryInfo,
    userDetail: store.userDetail,
    initialValues: store.userDetail,
    authUser: store.loggedIn
  }
}

function mapDispatchToProps(dispatch) {

  return bindActionCreators({
    getAllDeliveryInfo: getAllDeliveryInfo,
    getUserDetail: getUserDetail,
    editProfileWithDispatch: editProfile,
    changeStage: changeStage
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Orders);
