import React from 'react';
import {Button, Collapse, Space, Spin, Steps} from "antd";
import {Link} from "react-router-dom";
import {connect} from "react-redux";
import {getOneDeliveryInfo} from "../actions/deliveryInfo.action";

const { Step } = Steps;
const { Panel } = Collapse;

class MyOrderDisplay extends React.Component {

  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount(){
    this.props.getOneDeliveryInfo();
    console.log(this.props.deliveryInfo);
  }

  handleClick() {
    // window.location.replace("/kitchen-status");
    this.props.history.push("/kitchen-status");
  }

  callback(key) {
    console.log(key);
  }

  render () {
    return (
      <div>
        <Collapse onChange={this.callback}>
          <Panel header="Order 1" key="1">
            <Steps current={2}>
              <Step title="Payment Finished!" description="Ordered at: 2020-05-19 18:43:31." />
              <Step title="Your Order is on the way!" description="EST arrived time: 2020-05-19 20:30:00." />
              <Step title="Done!" description="This is a description." />
            </Steps>
          </Panel>
          <Panel header="Order 2" key="2">
            <Steps current={2}>
              <Step title="Payment Finished!" description="Ordered at: 2020-05-19 18:43:31." />
              <Step title="Your Order is on the way!" description="EST arrived time: 2020-05-19 20:30:00." />
              <Step title="Done!" description="This is a description." />
            </Steps>
          </Panel>

          {
            this.props.deliveryInfo && this.props.deliveryInfo.userId === this.props.authUser.id
            ?<Panel header="Order 3" key="3">
              <Steps current={1}>
                <Step title="Payment Finished!" description={`Ordered number: #${this.props.deliveryInfo.orderNum}.`}/>
                <Step title="Kitchen is preparing your food!" description={
                  <Space size="middle">
                    <Spin size="large"/>
                    <Button onClick={this.handleClick}>Check it from Kitchen now!!</Button>
                  </Space>
                }/>
                <Step title="Your Order is on the way!" description={`EST arrived time: ${this.props.deliveryInfo.time}.`} />
                <Step title="Done!" description="Enjoy your food!." />
              </Steps>
            </Panel>
              : null
          }

        </Collapse>

      </div>
    );
  }
}

function mapStateToProps(store) {
  return {
    authUser: store.loggedIn,
    deliveryInfo: store.deliveryInfo,
  };
}

export default connect(mapStateToProps, {getOneDeliveryInfo})(MyOrderDisplay);
